import React, { useContext, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import DebtContext from '../../context/debt/debtContext';
import FamilyContext from '../../context/family/familyContext';

const DebtDetails = () => {
  const debtContext = useContext(DebtContext);
  const familyContext = useContext(FamilyContext);
  const { id } = useParams();

  const { getDebt, currentDebt, loading } = debtContext;
  const { getMembers, members } = familyContext;

  useEffect(() => {
    getDebt(id);
    // eslint-disable-next-line
  }, [id]);

  useEffect(() => {
    if (currentDebt && currentDebt.familyId) {
      getMembers(currentDebt.familyId);
    }
    // eslint-disable-next-line
  }, [currentDebt]);

  if (loading || !currentDebt || !members) {
    return <div className="container">Loading...</div>;
  }

  // Find member names for debt display
  const getMemberName = (userId) => {
    const member = members.find(m => m._id === userId);
    return member ? member.name : 'Unknown';
  };

  return (
    <div className="container">
      <h1 className="text-primary">Debt Details</h1>
      
      <div className="card">
        <h3>Period Information</h3>
        <p><strong>Start Date:</strong> {new Date(currentDebt.startDate).toLocaleDateString()}</p>
        <p><strong>End Date:</strong> {new Date(currentDebt.endDate).toLocaleDateString()}</p>
        <p><strong>Status:</strong> {currentDebt.finalized ? 'Finalized' : 'Not Finalized'}</p>
        {currentDebt.finalized && (
          <p><strong>Finalized Date:</strong> {new Date(currentDebt.finalizedDate).toLocaleDateString()}</p>
        )}
      </div>
      
      <div className="card">
        <h3>Simplified Debts</h3>
        {currentDebt.simplifiedDebts.length === 0 ? (
          <p>No debts to settle! Everyone is even.</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>From</th>
                <th>To</th>
                <th>Amount</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {currentDebt.simplifiedDebts.map((debt, index) => (
                <tr key={index}>
                  <td>{getMemberName(debt.from)}</td>
                  <td>{getMemberName(debt.to)}</td>
                  <td>${debt.amount.toFixed(2)}</td>
                  <td>{debt.settled ? 'Settled' : 'Pending'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
      
      <div className="card">
        <h3>Original Expenses</h3>
        <p>This debt calculation includes {currentDebt.expenseIds.length} expenses.</p>
      </div>
    </div>
  );
};

export default DebtDetails;
