import React, { useContext, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';
import { toast } from 'react-toastify';

const FamilyDetails = () => {
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const navigate = useNavigate();
  const { id } = useParams();

  const { getFamily, currentFamily, leaveFamily, error, loading } = familyContext;
  const { user } = authContext;

  useEffect(() => {
    getFamily(id);
    // eslint-disable-next-line
  }, [id]);

  const onLeaveFamily = async () => {
    if (window.confirm('Are you sure you want to leave this family?')) {
      try {
        await leaveFamily(id);
        toast.success('Successfully left family');
        navigate('/dashboard');
      } catch (err) {
        toast.error(error || 'Could not leave family');
      }
    }
  };

  if (loading || !currentFamily) {
    return <div className="container">Loading...</div>;
  }

  const isHead = currentFamily.headOfFamily === user._id;

  return (
    <div className="container">
      <div className="family-header">
        <h1 className="text-primary">{currentFamily.name}</h1>
        <div>
          <Link to={`/families/${id}/members`} className="btn btn-dark">
            View Members
          </Link>
          {!isHead && (
            <button onClick={onLeaveFamily} className="btn btn-danger">
              Leave Family
            </button>
          )}
        </div>
      </div>

      {isHead && (
        <div className="family-invite">
          <h3>Invite Code</h3>
          <p>Share this code with others to invite them to your family:</p>
          <div className="code-display">
            <code>{currentFamily.inviteCode}</code>
          </div>
        </div>
      )}

      <div className="card">
        <h3>Family Options</h3>
        <div className="grid-2">
          <div>
            <h4>Expenses</h4>
            <Link to={`/expenses/family/${id}`} className="btn btn-primary">
              View Expenses
            </Link>
            <Link to={`/expenses/create/${id}`} className="btn btn-dark">
              Add Expense
            </Link>
            <Link to={`/expenses/summary/${id}`} className="btn btn-light">
              Expense Summary
            </Link>
          </div>
          <div>
            <h4>Debts</h4>
            <Link to={`/debts/calculate/${id}`} className="btn btn-primary">
              Calculate Debts
            </Link>
            <Link to={`/debts/history/${id}`} className="btn btn-dark">
              Debt History
            </Link>
          </div>
        </div>
        <div className="grid-2 my-2">
          <div>
            <h4>Budget</h4>
            <Link to={`/budgets/family/${id}`} className="btn btn-primary">
              View Budgets
            </Link>
            <Link to={`/budgets/create/${id}`} className="btn btn-dark">
              Create Budget
            </Link>
          </div>
          <div>
            <h4>Recurring Expenses</h4>
            <Link to={`/recurring/family/${id}`} className="btn btn-primary">
              View Recurring
            </Link>
            <Link to={`/recurring/create/${id}`} className="btn btn-dark">
              Add Recurring
            </Link>
          </div>
        </div>
        <div className="my-2">
          <h4>Export Data</h4>
          <Link to={`/exports/${id}`} className="btn btn-primary">
            Export Options
          </Link>
        </div>
      </div>
    </div>
  );
};

export default FamilyDetails;
