import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// Auth Components
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import PrivateRoute from './components/routing/PrivateRoute';

// Layout Components
import Navbar from './components/layout/Navbar';
import Landing from './components/layout/Landing';
import Dashboard from './components/dashboard/Dashboard';

// Family Components
import FamilyCreate from './components/families/FamilyCreate';
import FamilyJoin from './components/families/FamilyJoin';
import FamilyDetails from './components/families/FamilyDetails';
import FamilyMembers from './components/families/FamilyMembers';

// Expense Components
import ExpenseCreate from './components/expenses/ExpenseCreate';
import ExpenseList from './components/expenses/ExpenseList';
import ExpenseDetails from './components/expenses/ExpenseDetails';
import ExpenseSummary from './components/expenses/ExpenseSummary';

// Debt Components
import DebtCalculate from './components/debts/DebtCalculate';
import DebtHistory from './components/debts/DebtHistory';
import DebtDetails from './components/debts/DebtDetails';

// Budget Components
import BudgetCreate from './components/budgets/BudgetCreate';
import BudgetList from './components/budgets/BudgetList';
import BudgetStatus from './components/budgets/BudgetStatus';

// Recurring Expense Components
import RecurringCreate from './components/recurring/RecurringCreate';
import RecurringList from './components/recurring/RecurringList';

// Export Components
import ExportData from './components/exports/ExportData';

// State Context
import AuthState from './context/auth/AuthState';
import FamilyState from './context/family/FamilyState';
import ExpenseState from './context/expense/ExpenseState';
import DebtState from './context/debt/DebtState';
import BudgetState from './context/budget/BudgetState';
import RecurringState from './context/recurring/RecurringState';

import './App.css';

const App = () => {
  return (
    <AuthState>
      <FamilyState>
        <ExpenseState>
          <DebtState>
            <BudgetState>
              <RecurringState>
                <Router>
                  <div className="App">
                    <Navbar />
                    <ToastContainer position="top-right" autoClose={3000} />
                    <Routes>
                      <Route path="/" element={<Landing />} />
                      <Route path="/register" element={<Register />} />
                      <Route path="/login" element={<Login />} />
                      <Route path="/dashboard" element={<PrivateRoute><Dashboard /></PrivateRoute>} />
                      
                      {/* Family Routes */}
                      <Route path="/families/create" element={<PrivateRoute><FamilyCreate /></PrivateRoute>} />
                      <Route path="/families/join" element={<PrivateRoute><FamilyJoin /></PrivateRoute>} />
                      <Route path="/families/:id" element={<PrivateRoute><FamilyDetails /></PrivateRoute>} />
                      <Route path="/families/:id/members" element={<PrivateRoute><FamilyMembers /></PrivateRoute>} />
                      
                      {/* Expense Routes */}
                      <Route path="/expenses/create/:familyId" element={<PrivateRoute><ExpenseCreate /></PrivateRoute>} />
                      <Route path="/expenses/family/:familyId" element={<PrivateRoute><ExpenseList /></PrivateRoute>} />
                      <Route path="/expenses/:id" element={<PrivateRoute><ExpenseDetails /></PrivateRoute>} />
                      <Route path="/expenses/summary/:familyId" element={<PrivateRoute><ExpenseSummary /></PrivateRoute>} />
                      
                      {/* Debt Routes */}
                      <Route path="/debts/calculate/:familyId" element={<PrivateRoute><DebtCalculate /></PrivateRoute>} />
                      <Route path="/debts/history/:familyId" element={<PrivateRoute><DebtHistory /></PrivateRoute>} />
                      <Route path="/debts/:id" element={<PrivateRoute><DebtDetails /></PrivateRoute>} />
                      
                      {/* Budget Routes */}
                      <Route path="/budgets/create/:familyId" element={<PrivateRoute><BudgetCreate /></PrivateRoute>} />
                      <Route path="/budgets/family/:familyId" element={<PrivateRoute><BudgetList /></PrivateRoute>} />
                      <Route path="/budgets/status/:familyId/:month/:year" element={<PrivateRoute><BudgetStatus /></PrivateRoute>} />
                      
                      {/* Recurring Expense Routes */}
                      <Route path="/recurring/create/:familyId" element={<PrivateRoute><RecurringCreate /></PrivateRoute>} />
                      <Route path="/recurring/family/:familyId" element={<PrivateRoute><RecurringList /></PrivateRoute>} />
                      
                      {/* Export Routes */}
                      <Route path="/exports/:familyId" element={<PrivateRoute><ExportData /></PrivateRoute>} />
                    </Routes>
                  </div>
                </Router>
              </RecurringState>
            </BudgetState>
          </DebtState>
        </ExpenseState>
      </FamilyState>
    </AuthState>
  );
};

export default App;
