import React, { useContext, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import RecurringContext from '../../context/recurring/recurringContext';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';
import { toast } from 'react-toastify';

const RecurringList = () => {
  const recurringContext = useContext(RecurringContext);
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const { familyId } = useParams();

  const { getRecurringExpenses, recurringExpenses, deleteRecurring, generateExpense, loading } = recurringContext;
  const { getFamily, currentFamily } = familyContext;
  const { user } = authContext;

  useEffect(() => {
    getFamily(familyId);
    getRecurringExpenses(familyId);
    // eslint-disable-next-line
  }, [familyId]);

  const onDeleteRecurring = async (id) => {
    if (window.confirm('Are you sure you want to delete this recurring expense?')) {
      try {
        await deleteRecurring(id);
        toast.success('Recurring expense deleted');
      } catch (err) {
        toast.error('Failed to delete recurring expense');
      }
    }
  };

  const onGenerateExpense = async (id) => {
    try {
      const expense = await generateExpense(id);
      toast.success('Expense generated successfully');
      return expense;
    } catch (err) {
      toast.error('Failed to generate expense');
    }
  };

  if (loading || !currentFamily) {
    return <div className="container">Loading...</div>;
  }

  // Format frequency for display
  const formatFrequency = (recurring) => {
    switch (recurring.frequency) {
      case 'daily':
        return 'Daily';
      case 'weekly':
        const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        return `Weekly on ${days[recurring.dayOfWeek]}`;
      case 'monthly':
        return `Monthly on day ${recurring.dayOfMonth}`;
      case 'yearly':
        return `Yearly on ${new Date(recurring.startDate).toLocaleDateString(undefined, { month: 'long', day: 'numeric' })}`;
      default:
        return recurring.frequency;
    }
  };

  // Group recurring expenses by active status
  const activeRecurring = recurringExpenses.filter(r => r.isActive);
  const inactiveRecurring = recurringExpenses.filter(r => !r.isActive);

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Recurring Expenses</h1>
      
      <div className="my-2">
        <Link to={`/recurring/create/${familyId}`} className="btn btn-primary">
          Create New Recurring Expense
        </Link>
      </div>
      
      <div className="card">
        <h3>Active Recurring Expenses</h3>
        {activeRecurring.length === 0 ? (
          <p>No active recurring expenses found.</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>Title</th>
                <th>Amount</th>
                <th>Category</th>
                <th>Frequency</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {activeRecurring.map(recurring => (
                <tr key={recurring._id}>
                  <td>{recurring.title}</td>
                  <td>${recurring.amount.toFixed(2)}</td>
                  <td>{recurring.category}</td>
                  <td>{formatFrequency(recurring)}</td>
                  <td>
                    <button
                      onClick={() => onGenerateExpense(recurring._id)}
                      className="btn btn-success btn-sm"
                    >
                      Generate Expense
                    </button>
                    <button
                      onClick={() => onDeleteRecurring(recurring._id)}
                      className="btn btn-danger btn-sm"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
      
      {inactiveRecurring.length > 0 && (
        <div className="card">
          <h3>Inactive Recurring Expenses</h3>
          <table>
            <thead>
              <tr>
                <th>Title</th>
                <th>Amount</th>
                <th>Category</th>
                <th>Frequency</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {inactiveRecurring.map(recurring => (
                <tr key={recurring._id}>
                  <td>{recurring.title}</td>
                  <td>${recurring.amount.toFixed(2)}</td>
                  <td>{recurring.category}</td>
                  <td>{formatFrequency(recurring)}</td>
                  <td>
                    <button
                      onClick={() => onDeleteRecurring(recurring._id)}
                      className="btn btn-danger btn-sm"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
      
      <div className="card bg-light my-2">
        <h3>About Recurring Expenses</h3>
        <p>
          Due to system limitations, recurring expenses are not automatically generated on their scheduled dates.
          Instead, you must manually trigger the generation of expenses using the "Generate Expense" button.
        </p>
        <p>
          When you click "Generate Expense", a new expense will be created with the current date based on the
          recurring expense template.
        </p>
      </div>
    </div>
  );
};

export default RecurringList;
