import React, { useReducer } from 'react';
import axios from 'axios';
import DebtContext from './debtContext';
import debtReducer from './debtReducer';
import {
  GET_CURRENT_DEBTS,
  CALCULATE_DEBTS,
  SETTLE_DEBT,
  FINALIZE_DEBTS,
  GET_DEBT_HISTORY,
  GET_DEBT,
  DEBT_ERROR,
  CLEAR_DEBTS
} from '../types';

const DebtState = props => {
  const initialState = {
    currentDebts: null,
    debtHistory: [],
    currentDebt: null,
    loading: true,
    error: null
  };

  const [state, dispatch] = useReducer(debtReducer, initialState);

  // Get current debts
  const getCurrentDebts = async (familyId, period = {}) => {
    try {
      let url = `/api/debts/current/${familyId}`;
      
      // Add query parameters for period
      const queryParams = [];
      if (period.startDate) queryParams.push(`startDate=${period.startDate}`);
      if (period.endDate) queryParams.push(`endDate=${period.endDate}`);
      
      if (queryParams.length > 0) {
        url += `?${queryParams.join('&')}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_CURRENT_DEBTS,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Calculate and simplify debts
  const calculateDebts = async (familyId, period) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post(`/api/debts/calculate/${familyId}`, period, config);

      dispatch({
        type: CALCULATE_DEBTS,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Settle specific debt
  const settleDebt = async (debtId, debtIndex) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post(`/api/debts/settle/${debtId}`, { debtIndex }, config);

      dispatch({
        type: SETTLE_DEBT,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Finalize debts
  const finalizeDebts = async debtId => {
    try {
      const res = await axios.post(`/api/debts/finalize/${debtId}`);

      dispatch({
        type: FINALIZE_DEBTS,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Get debt history
  const getDebtHistory = async (familyId, filters = {}) => {
    try {
      let url = `/api/debts/history/${familyId}`;
      
      // Add query parameters for filters
      const queryParams = [];
      if (filters.startDate) queryParams.push(`startDate=${filters.startDate}`);
      if (filters.endDate) queryParams.push(`endDate=${filters.endDate}`);
      if (filters.finalized !== undefined) queryParams.push(`finalized=${filters.finalized}`);
      
      if (queryParams.length > 0) {
        url += `?${queryParams.join('&')}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_DEBT_HISTORY,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Get specific debt
  const getDebt = async id => {
    try {
      const res = await axios.get(`/api/debts/${id}`);

      dispatch({
        type: GET_DEBT,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: DEBT_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Clear debts
  const clearDebts = () => {
    dispatch({ type: CLEAR_DEBTS });
  };

  return (
    <DebtContext.Provider
      value={{
        currentDebts: state.currentDebts,
        debtHistory: state.debtHistory,
        currentDebt: state.currentDebt,
        loading: state.loading,
        error: state.error,
        getCurrentDebts,
        calculateDebts,
        settleDebt,
        finalizeDebts,
        getDebtHistory,
        getDebt,
        clearDebts
      }}
    >
      {props.children}
    </DebtContext.Provider>
  );
};

export default DebtState;
