import React, { useContext, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import ExpenseContext from '../../context/expense/expenseContext';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';
import { toast } from 'react-toastify';

const ExpenseDetails = () => {
  const expenseContext = useContext(ExpenseContext);
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const navigate = useNavigate();
  const { id } = useParams();

  const { getExpense, currentExpense, deleteExpense, loading } = expenseContext;
  const { getMembers, members } = familyContext;
  const { user } = authContext;

  useEffect(() => {
    getExpense(id);
    // eslint-disable-next-line
  }, [id]);

  useEffect(() => {
    if (currentExpense && currentExpense.familyId) {
      getMembers(currentExpense.familyId);
    }
    // eslint-disable-next-line
  }, [currentExpense]);

  const onDeleteExpense = async () => {
    if (window.confirm('Are you sure you want to delete this expense?')) {
      try {
        await deleteExpense(id);
        toast.success('Expense deleted');
        navigate(`/expenses/family/${currentExpense.familyId}`);
      } catch (err) {
        toast.error('Failed to delete expense');
      }
    }
  };

  if (loading || !currentExpense || !members) {
    return <div className="container">Loading...</div>;
  }

  // Check if user is the creator of the expense
  const isCreator = currentExpense.createdBy === user._id;

  // Find member names for sharing display
  const getMemberName = (userId) => {
    const member = members.find(m => m._id === userId);
    return member ? member.name : 'Unknown';
  };

  return (
    <div className="container">
      <h1 className="text-primary">Expense Details</h1>
      
      <div className="card">
        <h3>{currentExpense.description}</h3>
        <div className="grid-2">
          <div>
            <p><strong>Amount:</strong> ${currentExpense.amount.toFixed(2)}</p>
            <p><strong>Category:</strong> {currentExpense.category}</p>
            <p><strong>Date:</strong> {new Date(currentExpense.date).toLocaleDateString()}</p>
            <p><strong>Created By:</strong> {getMemberName(currentExpense.createdBy)}</p>
          </div>
          <div>
            <h4>Sharing Details</h4>
            <table>
              <thead>
                <tr>
                  <th>Member</th>
                  <th>Share Type</th>
                  <th>Share Value</th>
                  <th>Amount</th>
                </tr>
              </thead>
              <tbody>
                {currentExpense.sharedAmongst.map((share, index) => (
                  <tr key={index}>
                    <td>{getMemberName(share.user)}</td>
                    <td>{share.shareType}</td>
                    <td>{share.shareType === 'equal' ? 
                        `${(share.shareValue * 100).toFixed(0)}%` : 
                        `${(share.shareValue * 100).toFixed(0)}%`}
                    </td>
                    <td>${(currentExpense.amount * share.shareValue).toFixed(2)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        
        {isCreator && (
          <div className="my-1">
            <button onClick={onDeleteExpense} className="btn btn-danger">
              Delete Expense
            </button>
          </div>
        )}
        
        <button 
          onClick={() => navigate(`/expenses/family/${currentExpense.familyId}`)} 
          className="btn btn-dark"
        >
          Back to Expenses
        </button>
      </div>
    </div>
  );
};

export default ExpenseDetails;
