import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import DebtContext from '../../context/debt/debtContext';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';
import { toast } from 'react-toastify';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const DebtCalculate = () => {
  const debtContext = useContext(DebtContext);
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const { familyId } = useParams();

  const { calculateDebts, currentDebt, settleDebt, finalizeDebts, loading } = debtContext;
  const { getFamily, currentFamily, getMembers, members } = familyContext;
  const { user } = authContext;

  const [period, setPeriod] = useState({
    startDate: new Date(new Date().setDate(1)), // First day of current month
    endDate: new Date()
  });

  useEffect(() => {
    getFamily(familyId);
    getMembers(familyId);
    // eslint-disable-next-line
  }, [familyId]);

  const onDateChange = (date, field) => {
    setPeriod({ ...period, [field]: date });
  };

  const onCalculateDebts = async () => {
    try {
      await calculateDebts(familyId, period);
      toast.success('Debts calculated successfully');
    } catch (err) {
      toast.error('Failed to calculate debts');
    }
  };

  const onSettleDebt = async (debtIndex) => {
    if (!currentDebt) return;
    
    try {
      await settleDebt(currentDebt._id, debtIndex);
      toast.success('Debt marked as settled');
    } catch (err) {
      toast.error('Failed to settle debt');
    }
  };

  const onFinalizeDebts = async () => {
    if (!currentDebt) return;
    
    if (window.confirm('Are you sure you want to finalize these debts? This will mark all expenses in this period as settled.')) {
      try {
        await finalizeDebts(currentDebt._id);
        toast.success('Debts finalized successfully');
      } catch (err) {
        toast.error('Failed to finalize debts');
      }
    }
  };

  if (loading || !currentFamily || !members) {
    return <div className="container">Loading...</div>;
  }

  // Check if user is head of family
  const isHead = currentFamily.headOfFamily === user._id;

  // Find member names for debt display
  const getMemberName = (userId) => {
    const member = members.find(m => m._id === userId);
    return member ? member.name : 'Unknown';
  };

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Calculate Debts</h1>
      
      <div className="card">
        <h3>Select Period</h3>
        <div className="grid-2">
          <div className="form-group">
            <label>Start Date</label>
            <DatePicker
              selected={period.startDate}
              onChange={date => onDateChange(date, 'startDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
            />
          </div>
          <div className="form-group">
            <label>End Date</label>
            <DatePicker
              selected={period.endDate}
              onChange={date => onDateChange(date, 'endDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
            />
          </div>
        </div>
        <button onClick={onCalculateDebts} className="btn btn-primary">
          Calculate Debts
        </button>
      </div>
      
      {currentDebt && (
        <div className="card">
          <h3>Debt Summary</h3>
          <p><strong>Period:</strong> {new Date(currentDebt.startDate).toLocaleDateString()} to {new Date(currentDebt.endDate).toLocaleDateString()}</p>
          <p><strong>Status:</strong> {currentDebt.finalized ? 'Finalized' : 'Not Finalized'}</p>
          
          <h4>Simplified Debts</h4>
          {currentDebt.simplifiedDebts.length === 0 ? (
            <p>No debts to settle! Everyone is even.</p>
          ) : (
            <table>
              <thead>
                <tr>
                  <th>From</th>
                  <th>To</th>
                  <th>Amount</th>
                  <th>Status</th>
                  {isHead && <th>Actions</th>}
                </tr>
              </thead>
              <tbody>
                {currentDebt.simplifiedDebts.map((debt, index) => (
                  <tr key={index}>
                    <td>{getMemberName(debt.from)}</td>
                    <td>{getMemberName(debt.to)}</td>
                    <td>${debt.amount.toFixed(2)}</td>
                    <td>{debt.settled ? 'Settled' : 'Pending'}</td>
                    {isHead && !debt.settled && !currentDebt.finalized && (
                      <td>
                        <button
                          onClick={() => onSettleDebt(index)}
                          className="btn btn-dark btn-sm"
                        >
                          Mark Settled
                        </button>
                      </td>
                    )}
                  </tr>
                ))}
              </tbody>
            </table>
          )}
          
          {isHead && !currentDebt.finalized && (
            <div className="my-1">
              <button onClick={onFinalizeDebts} className="btn btn-primary">
                Finalize All Debts
              </button>
              <p className="form-text">
                This will mark all expenses in this period as settled and record the debt history.
              </p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default DebtCalculate;
