import React, { useReducer } from 'react';
import axios from 'axios';
import RecurringContext from './recurringContext';
import recurringReducer from './recurringReducer';
import {
  GET_RECURRING,
  GET_RECURRING_EXPENSE,
  CREATE_RECURRING,
  UPDATE_RECURRING,
  DELETE_RECURRING,
  GENERATE_EXPENSE,
  RECURRING_ERROR,
  CLEAR_RECURRING
} from '../types';

const RecurringState = props => {
  const initialState = {
    recurringExpenses: [],
    currentRecurring: null,
    loading: true,
    error: null
  };

  const [state, dispatch] = useReducer(recurringReducer, initialState);

  // Get recurring expenses for a family
  const getRecurringExpenses = async (familyId, active) => {
    try {
      let url = `/api/recurring/family/${familyId}`;
      
      if (active !== undefined) {
        url += `?active=${active}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_RECURRING,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Get specific recurring expense
  const getRecurringExpense = async id => {
    try {
      const res = await axios.get(`/api/recurring/${id}`);

      dispatch({
        type: GET_RECURRING_EXPENSE,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Create recurring expense
  const createRecurring = async recurringExpense => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post('/api/recurring', recurringExpense, config);

      dispatch({
        type: CREATE_RECURRING,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Update recurring expense
  const updateRecurring = async (id, recurringExpense) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.put(`/api/recurring/${id}`, recurringExpense, config);

      dispatch({
        type: UPDATE_RECURRING,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Delete recurring expense
  const deleteRecurring = async id => {
    try {
      await axios.delete(`/api/recurring/${id}`);

      dispatch({
        type: DELETE_RECURRING,
        payload: id
      });
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Generate expense from recurring
  const generateExpense = async id => {
    try {
      const res = await axios.post(`/api/recurring/${id}/generate`);

      dispatch({
        type: GENERATE_EXPENSE,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: RECURRING_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Clear recurring expenses
  const clearRecurring = () => {
    dispatch({ type: CLEAR_RECURRING });
  };

  return (
    <RecurringContext.Provider
      value={{
        recurringExpenses: state.recurringExpenses,
        currentRecurring: state.currentRecurring,
        loading: state.loading,
        error: state.error,
        getRecurringExpenses,
        getRecurringExpense,
        createRecurring,
        updateRecurring,
        deleteRecurring,
        generateExpense,
        clearRecurring
      }}
    >
      {props.children}
    </RecurringContext.Provider>
  );
};

export default RecurringState;
