import React, { useContext, useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import ExpenseContext from '../../context/expense/expenseContext';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const ExpenseList = () => {
  const expenseContext = useContext(ExpenseContext);
  const familyContext = useContext(FamilyContext);
  const { familyId } = useParams();

  const { expenses, getExpenses, deleteExpense, loading } = expenseContext;
  const { getFamily, currentFamily, getMembers, members } = familyContext;

  const [filters, setFilters] = useState({
    startDate: null,
    endDate: null,
    category: '',
    userId: ''
  });

  useEffect(() => {
    getFamily(familyId);
    getMembers(familyId);
    getExpenses(familyId);
    // eslint-disable-next-line
  }, [familyId]);

  const onFilterChange = e => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  const onDateChange = (date, field) => {
    setFilters({ ...filters, [field]: date });
  };

  const applyFilters = () => {
    getExpenses(familyId, filters);
  };

  const resetFilters = () => {
    setFilters({
      startDate: null,
      endDate: null,
      category: '',
      userId: ''
    });
    getExpenses(familyId);
  };

  const onDeleteExpense = async (id) => {
    if (window.confirm('Are you sure you want to delete this expense?')) {
      try {
        await deleteExpense(id);
        toast.success('Expense deleted');
      } catch (err) {
        toast.error('Failed to delete expense');
      }
    }
  };

  if (loading || !currentFamily) {
    return <div className="container">Loading...</div>;
  }

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Expenses</h1>
      
      <div className="expense-filters card">
        <h3>Filter Expenses</h3>
        <div className="grid-2">
          <div className="form-group">
            <label>Start Date</label>
            <DatePicker
              selected={filters.startDate}
              onChange={date => onDateChange(date, 'startDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
              isClearable
            />
          </div>
          <div className="form-group">
            <label>End Date</label>
            <DatePicker
              selected={filters.endDate}
              onChange={date => onDateChange(date, 'endDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
              isClearable
            />
          </div>
        </div>
        <div className="grid-2">
          <div className="form-group">
            <label>Category</label>
            <select
              name="category"
              value={filters.category}
              onChange={onFilterChange}
            >
              <option value="">All Categories</option>
              <option value="Food">Food</option>
              <option value="Transportation">Transportation</option>
              <option value="Housing">Housing</option>
              <option value="Utilities">Utilities</option>
              <option value="Entertainment">Entertainment</option>
              <option value="Healthcare">Healthcare</option>
              <option value="Education">Education</option>
              <option value="Shopping">Shopping</option>
              <option value="Other">Other</option>
            </select>
          </div>
          <div className="form-group">
            <label>Paid By</label>
            <select
              name="userId"
              value={filters.userId}
              onChange={onFilterChange}
            >
              <option value="">All Members</option>
              {members && members.map(member => (
                <option key={member._id} value={member._id}>
                  {member.name}
                </option>
              ))}
            </select>
          </div>
        </div>
        <div>
          <button onClick={applyFilters} className="btn btn-primary">
            Apply Filters
          </button>
          <button onClick={resetFilters} className="btn btn-light">
            Reset Filters
          </button>
        </div>
      </div>
      
      <div className="my-2">
        <Link to={`/expenses/create/${familyId}`} className="btn btn-primary">
          Add New Expense
        </Link>
        <Link to={`/expenses/summary/${familyId}`} className="btn btn-dark">
          View Summary
        </Link>
      </div>
      
      <div className="card">
        <h3>Expense List</h3>
        {expenses.length === 0 ? (
          <p>No expenses found. Add some expenses to get started!</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>Date</th>
                <th>Description</th>
                <th>Category</th>
                <th>Amount</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {expenses.map(expense => (
                <tr key={expense._id}>
                  <td>{new Date(expense.date).toLocaleDateString()}</td>
                  <td>{expense.description}</td>
                  <td>{expense.category}</td>
                  <td>${expense.amount.toFixed(2)}</td>
                  <td>
                    <Link to={`/expenses/${expense._id}`} className="btn btn-dark btn-sm">
                      Details
                    </Link>
                    <button
                      onClick={() => onDeleteExpense(expense._id)}
                      className="btn btn-danger btn-sm"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default ExpenseList;
