import {
  GET_BUDGETS,
  GET_BUDGET,
  CREATE_BUDGET,
  UPDATE_BUDGET,
  DELETE_BUDGET,
  GET_BUDGET_STATUS,
  BUDGET_ERROR,
  CLEAR_BUDGETS
} from '../types';

const budgetReducer = (state, action) => {
  switch (action.type) {
    case GET_BUDGETS:
      return {
        ...state,
        budgets: action.payload,
        loading: false
      };
    case GET_BUDGET:
      return {
        ...state,
        currentBudget: action.payload,
        loading: false
      };
    case CREATE_BUDGET:
      return {
        ...state,
        budgets: [action.payload, ...state.budgets],
        loading: false
      };
    case UPDATE_BUDGET:
      return {
        ...state,
        budgets: state.budgets.map(budget =>
          budget._id === action.payload._id ? action.payload : budget
        ),
        currentBudget: action.payload,
        loading: false
      };
    case DELETE_BUDGET:
      return {
        ...state,
        budgets: state.budgets.filter(
          budget => budget._id !== action.payload
        ),
        loading: false
      };
    case GET_BUDGET_STATUS:
      return {
        ...state,
        budgetStatus: action.payload,
        loading: false
      };
    case BUDGET_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    case CLEAR_BUDGETS:
      return {
        ...state,
        budgets: [],
        currentBudget: null,
        budgetStatus: null,
        error: null
      };
    default:
      return state;
  }
};

export default budgetReducer;
