import {
  GET_FAMILIES,
  GET_FAMILY,
  CREATE_FAMILY,
  JOIN_FAMILY,
  FAMILY_ERROR,
  CLEAR_FAMILY,
  GET_MEMBERS,
  LEAVE_FAMILY,
  TRANSFER_OWNERSHIP
} from '../types';

const familyReducer = (state, action) => {
  switch (action.type) {
    case GET_FAMILIES:
      return {
        ...state,
        families: action.payload,
        loading: false
      };
    case GET_FAMILY:
      return {
        ...state,
        currentFamily: action.payload,
        loading: false
      };
    case CREATE_FAMILY:
      return {
        ...state,
        families: [action.payload, ...state.families],
        currentFamily: action.payload,
        loading: false
      };
    case JOIN_FAMILY:
      return {
        ...state,
        families: [action.payload, ...state.families],
        currentFamily: action.payload,
        loading: false
      };
    case GET_MEMBERS:
      return {
        ...state,
        members: action.payload,
        loading: false
      };
    case LEAVE_FAMILY:
      return {
        ...state,
        families: state.families.filter(
          family => family._id !== action.payload
        ),
        currentFamily: null,
        loading: false
      };
    case TRANSFER_OWNERSHIP:
      return {
        ...state,
        currentFamily: action.payload,
        loading: false
      };
    case FAMILY_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    case CLEAR_FAMILY:
      return {
        ...state,
        currentFamily: null,
        members: [],
        error: null
      };
    default:
      return state;
  }
};

export default familyReducer;
