import {
  GET_CURRENT_DEBTS,
  CALCULATE_DEBTS,
  SETTLE_DEBT,
  FINALIZE_DEBTS,
  GET_DEBT_HISTORY,
  GET_DEBT,
  DEBT_ERROR,
  CLEAR_DEBTS
} from '../types';

const debtReducer = (state, action) => {
  switch (action.type) {
    case GET_CURRENT_DEBTS:
      return {
        ...state,
        currentDebts: action.payload,
        loading: false
      };
    case CALCULATE_DEBTS:
      return {
        ...state,
        currentDebt: action.payload,
        loading: false
      };
    case SETTLE_DEBT:
    case FINALIZE_DEBTS:
      return {
        ...state,
        currentDebt: action.payload,
        loading: false
      };
    case GET_DEBT_HISTORY:
      return {
        ...state,
        debtHistory: action.payload,
        loading: false
      };
    case GET_DEBT:
      return {
        ...state,
        currentDebt: action.payload,
        loading: false
      };
    case DEBT_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    case CLEAR_DEBTS:
      return {
        ...state,
        currentDebts: null,
        debtHistory: [],
        currentDebt: null,
        error: null
      };
    default:
      return state;
  }
};

export default debtReducer;
