import React, { useState, useContext, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import ExpenseContext from '../../context/expense/expenseContext';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const ExpenseCreate = () => {
  const expenseContext = useContext(ExpenseContext);
  const familyContext = useContext(FamilyContext);
  const navigate = useNavigate();
  const { familyId } = useParams();

  const { addExpense, error, clearErrors } = expenseContext;
  const { getFamily, currentFamily, getMembers, members, loading } = familyContext;

  const [expense, setExpense] = useState({
    familyId,
    amount: '',
    description: '',
    category: '',
    date: new Date(),
    sharedAmongst: []
  });

  const { amount, description, category, date } = expense;

  const [sharingType, setSharingType] = useState('equal');

  useEffect(() => {
    getFamily(familyId);
    getMembers(familyId);

    if (error) {
      toast.error(error);
      clearErrors();
    }
    // eslint-disable-next-line
  }, [familyId, error]);

  useEffect(() => {
    if (members && members.length > 0) {
      // Initialize with equal sharing for all members
      const initialSharing = members.map(member => ({
        user: member._id,
        shareType: 'equal',
        shareValue: 1 / members.length
      }));
      
      setExpense(prev => ({
        ...prev,
        sharedAmongst: initialSharing
      }));
    }
  }, [members]);

  const onChange = e => {
    setExpense({ ...expense, [e.target.name]: e.target.value });
  };

  const onDateChange = date => {
    setExpense({ ...expense, date });
  };

  const onSharingTypeChange = e => {
    setSharingType(e.target.value);
    
    // Update sharing values based on type
    let updatedSharing;
    if (e.target.value === 'equal') {
      updatedSharing = members.map(member => ({
        user: member._id,
        shareType: 'equal',
        shareValue: 1 / members.length
      }));
    } else {
      // For custom, initialize with 0
      updatedSharing = members.map(member => ({
        user: member._id,
        shareType: 'custom',
        shareValue: 0
      }));
    }
    
    setExpense(prev => ({
      ...prev,
      sharedAmongst: updatedSharing
    }));
  };

  const onShareValueChange = (userId, value) => {
    const updatedSharing = expense.sharedAmongst.map(share => 
      share.user === userId ? { ...share, shareValue: parseFloat(value) } : share
    );
    
    setExpense(prev => ({
      ...prev,
      sharedAmongst: updatedSharing
    }));
  };

  const onSubmit = async e => {
    e.preventDefault();
    
    if (amount === '' || description === '' || category === '') {
      toast.error('Please fill in all required fields');
      return;
    }
    
    // Validate sharing values for custom type
    if (sharingType === 'custom') {
      const totalShare = expense.sharedAmongst.reduce((sum, share) => sum + share.shareValue, 0);
      if (Math.abs(totalShare - 1) > 0.01) {
        toast.error('Share values must add up to 100%');
        return;
      }
    }
    
    try {
      await addExpense({
        ...expense,
        amount: parseFloat(amount)
      });
      
      toast.success('Expense added successfully!');
      navigate(`/expenses/family/${familyId}`);
    } catch (err) {
      // Error is handled by context
    }
  };

  if (loading || !members) {
    return <div className="container">Loading...</div>;
  }

  return (
    <div className='container'>
      <h1 className='text-primary'>Add Expense</h1>
      <p className='lead'>Add a new expense for {currentFamily && currentFamily.name}</p>
      
      <form onSubmit={onSubmit}>
        <div className='form-group'>
          <label htmlFor='amount'>Amount *</label>
          <input
            type='number'
            step='0.01'
            name='amount'
            value={amount}
            onChange={onChange}
            required
          />
        </div>
        
        <div className='form-group'>
          <label htmlFor='description'>Description *</label>
          <input
            type='text'
            name='description'
            value={description}
            onChange={onChange}
            required
          />
        </div>
        
        <div className='form-group'>
          <label htmlFor='category'>Category *</label>
          <select
            name='category'
            value={category}
            onChange={onChange}
            required
          >
            <option value=''>Select Category</option>
            <option value='Food'>Food</option>
            <option value='Transportation'>Transportation</option>
            <option value='Housing'>Housing</option>
            <option value='Utilities'>Utilities</option>
            <option value='Entertainment'>Entertainment</option>
            <option value='Healthcare'>Healthcare</option>
            <option value='Education'>Education</option>
            <option value='Shopping'>Shopping</option>
            <option value='Other'>Other</option>
          </select>
        </div>
        
        <div className='form-group'>
          <label htmlFor='date'>Date *</label>
          <DatePicker
            selected={date}
            onChange={onDateChange}
            className='form-control'
            dateFormat='yyyy-MM-dd'
          />
        </div>
        
        <div className='form-group'>
          <label>Sharing Type</label>
          <div>
            <input
              type='radio'
              id='equal'
              name='sharingType'
              value='equal'
              checked={sharingType === 'equal'}
              onChange={onSharingTypeChange}
            />
            <label htmlFor='equal'>Equal Split</label>
            
            <input
              type='radio'
              id='custom'
              name='sharingType'
              value='custom'
              checked={sharingType === 'custom'}
              onChange={onSharingTypeChange}
              className='ml-2'
            />
            <label htmlFor='custom'>Custom Split</label>
          </div>
        </div>
        
        {sharingType === 'custom' && (
          <div className='form-group'>
            <label>Custom Share Values (must add up to 100%)</label>
            <table>
              <thead>
                <tr>
                  <th>Member</th>
                  <th>Share (%)</th>
                </tr>
              </thead>
              <tbody>
                {members.map(member => (
                  <tr key={member._id}>
                    <td>{member.name}</td>
                    <td>
                      <input
                        type='number'
                        step='0.01'
                        min='0'
                        max='1'
                        value={expense.sharedAmongst.find(share => share.user === member._id)?.shareValue || 0}
                        onChange={e => onShareValueChange(member._id, e.target.value)}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
        
        <input
          type='submit'
          value='Add Expense'
          className='btn btn-primary btn-block'
        />
      </form>
    </div>
  );
};

export default ExpenseCreate;
