import React, { useReducer } from 'react';
import axios from 'axios';
import BudgetContext from './budgetContext';
import budgetReducer from './budgetReducer';
import {
  GET_BUDGETS,
  GET_BUDGET,
  CREATE_BUDGET,
  UPDATE_BUDGET,
  DELETE_BUDGET,
  GET_BUDGET_STATUS,
  BUDGET_ERROR,
  CLEAR_BUDGETS
} from '../types';

const BudgetState = props => {
  const initialState = {
    budgets: [],
    currentBudget: null,
    budgetStatus: null,
    loading: true,
    error: null
  };

  const [state, dispatch] = useReducer(budgetReducer, initialState);

  // Get budgets for a family
  const getBudgets = async (familyId, year) => {
    try {
      let url = `/api/budgets/family/${familyId}`;
      
      if (year) {
        url += `?year=${year}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_BUDGETS,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Get specific budget
  const getBudget = async id => {
    try {
      const res = await axios.get(`/api/budgets/${id}`);

      dispatch({
        type: GET_BUDGET,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Create budget
  const createBudget = async budget => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post('/api/budgets', budget, config);

      dispatch({
        type: CREATE_BUDGET,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Update budget
  const updateBudget = async (id, budget) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.put(`/api/budgets/${id}`, budget, config);

      dispatch({
        type: UPDATE_BUDGET,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Delete budget
  const deleteBudget = async id => {
    try {
      await axios.delete(`/api/budgets/${id}`);

      dispatch({
        type: DELETE_BUDGET,
        payload: id
      });
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Get budget status
  const getBudgetStatus = async (familyId, month, year) => {
    try {
      const res = await axios.get(`/api/budgets/status/${familyId}/${month}/${year}`);

      dispatch({
        type: GET_BUDGET_STATUS,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: BUDGET_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Clear budgets
  const clearBudgets = () => {
    dispatch({ type: CLEAR_BUDGETS });
  };

  return (
    <BudgetContext.Provider
      value={{
        budgets: state.budgets,
        currentBudget: state.currentBudget,
        budgetStatus: state.budgetStatus,
        loading: state.loading,
        error: state.error,
        getBudgets,
        getBudget,
        createBudget,
        updateBudget,
        deleteBudget,
        getBudgetStatus,
        clearBudgets
      }}
    >
      {props.children}
    </BudgetContext.Provider>
  );
};

export default BudgetState;
