import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';

const FamilyCreate = () => {
  const familyContext = useContext(FamilyContext);
  const navigate = useNavigate();

  const { createFamily, error, clearErrors } = familyContext;

  const [family, setFamily] = useState({
    name: ''
  });

  const { name } = family;

  useEffect(() => {
    if (error) {
      toast.error(error);
      clearErrors();
    }
    // eslint-disable-next-line
  }, [error]);

  const onChange = e => setFamily({ ...family, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    if (name === '') {
      toast.error('Please enter a family name');
    } else {
      try {
        const newFamily = await createFamily({
          name
        });
        toast.success('Family created successfully!');
        navigate(`/families/${newFamily._id}`);
      } catch (err) {
        // Error is handled by context
      }
    }
  };

  return (
    <div className='container'>
      <h1 className='text-primary'>Create Family</h1>
      <p className='lead'>Create a new family group to track expenses together</p>
      <form onSubmit={onSubmit}>
        <div className='form-group'>
          <label htmlFor='name'>Family Name</label>
          <input
            type='text'
            name='name'
            value={name}
            onChange={onChange}
            required
          />
        </div>
        <input
          type='submit'
          value='Create Family'
          className='btn btn-primary btn-block'
        />
      </form>
    </div>
  );
};

export default FamilyCreate;
