import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';

const FamilyJoin = () => {
  const familyContext = useContext(FamilyContext);
  const navigate = useNavigate();

  const { joinFamily, error, clearErrors } = familyContext;

  const [inviteCode, setInviteCode] = useState('');

  useEffect(() => {
    if (error) {
      toast.error(error);
      clearErrors();
    }
    // eslint-disable-next-line
  }, [error]);

  const onChange = e => setInviteCode(e.target.value);

  const onSubmit = async e => {
    e.preventDefault();
    if (inviteCode === '') {
      toast.error('Please enter an invite code');
    } else {
      try {
        const joinedFamily = await joinFamily(inviteCode);
        toast.success('Successfully joined family!');
        navigate(`/families/${joinedFamily._id}`);
      } catch (err) {
        // Error is handled by context
      }
    }
  };

  return (
    <div className='container'>
      <h1 className='text-primary'>Join Family</h1>
      <p className='lead'>Join an existing family group with an invite code</p>
      <form onSubmit={onSubmit}>
        <div className='form-group'>
          <label htmlFor='inviteCode'>Family Invite Code</label>
          <input
            type='text'
            name='inviteCode'
            value={inviteCode}
            onChange={onChange}
            required
            placeholder='Enter the UUID invite code'
          />
        </div>
        <input
          type='submit'
          value='Join Family'
          className='btn btn-primary btn-block'
        />
      </form>
    </div>
  );
};

export default FamilyJoin;
