import React, { useState, useContext, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import RecurringContext from '../../context/recurring/recurringContext';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const RecurringCreate = () => {
  const recurringContext = useContext(RecurringContext);
  const familyContext = useContext(FamilyContext);
  const navigate = useNavigate();
  const { familyId } = useParams();

  const { createRecurring, error, clearErrors } = recurringContext;
  const { getFamily, currentFamily } = familyContext;

  const [recurring, setRecurring] = useState({
    familyId,
    title: '',
    amount: '',
    category: '',
    frequency: 'monthly',
    startDate: new Date(),
    dayOfMonth: new Date().getDate(),
    dayOfWeek: new Date().getDay(),
    isActive: true
  });

  const { title, amount, category, frequency, startDate, dayOfMonth, dayOfWeek } = recurring;

  useEffect(() => {
    getFamily(familyId);

    if (error) {
      toast.error(error);
      clearErrors();
    }
    // eslint-disable-next-line
  }, [familyId, error]);

  const onChange = e => {
    setRecurring({ ...recurring, [e.target.name]: e.target.value });
  };

  const onDateChange = date => {
    setRecurring({ 
      ...recurring, 
      startDate: date,
      dayOfMonth: date.getDate(),
      dayOfWeek: date.getDay()
    });
  };

  const onSubmit = async e => {
    e.preventDefault();
    
    if (title === '' || amount === '' || category === '') {
      toast.error('Please fill in all required fields');
      return;
    }
    
    try {
      await createRecurring({
        ...recurring,
        amount: parseFloat(amount)
      });
      
      toast.success('Recurring expense created successfully!');
      navigate(`/recurring/family/${familyId}`);
    } catch (err) {
      // Error is handled by context
    }
  };

  if (!currentFamily) {
    return <div className="container">Loading...</div>;
  }

  return (
    <div className='container'>
      <h1 className='text-primary'>Create Recurring Expense</h1>
      <p className='lead'>Create a new recurring expense for {currentFamily.name}</p>
      
      <form onSubmit={onSubmit}>
        <div className='form-group'>
          <label htmlFor='title'>Title *</label>
          <input
            type='text'
            name='title'
            value={title}
            onChange={onChange}
            required
          />
        </div>
        
        <div className='form-group'>
          <label htmlFor='amount'>Amount *</label>
          <input
            type='number'
            step='0.01'
            name='amount'
            value={amount}
            onChange={onChange}
            required
          />
        </div>
        
        <div className='form-group'>
          <label htmlFor='category'>Category *</label>
          <select
            name='category'
            value={category}
            onChange={onChange}
            required
          >
            <option value=''>Select Category</option>
            <option value='Food'>Food</option>
            <option value='Transportation'>Transportation</option>
            <option value='Housing'>Housing</option>
            <option value='Utilities'>Utilities</option>
            <option value='Entertainment'>Entertainment</option>
            <option value='Healthcare'>Healthcare</option>
            <option value='Education'>Education</option>
            <option value='Shopping'>Shopping</option>
            <option value='Other'>Other</option>
          </select>
        </div>
        
        <div className='form-group'>
          <label htmlFor='frequency'>Frequency *</label>
          <select
            name='frequency'
            value={frequency}
            onChange={onChange}
            required
          >
            <option value='daily'>Daily</option>
            <option value='weekly'>Weekly</option>
            <option value='monthly'>Monthly</option>
            <option value='yearly'>Yearly</option>
          </select>
        </div>
        
        <div className='form-group'>
          <label htmlFor='startDate'>Start Date *</label>
          <DatePicker
            selected={startDate}
            onChange={onDateChange}
            className='form-control'
            dateFormat='yyyy-MM-dd'
          />
        </div>
        
        {frequency === 'monthly' && (
          <div className='form-group'>
            <label htmlFor='dayOfMonth'>Day of Month</label>
            <input
              type='number'
              name='dayOfMonth'
              value={dayOfMonth}
              onChange={onChange}
              min='1'
              max='31'
            />
            <small className='form-text'>
              The day of the month this expense recurs (1-31)
            </small>
          </div>
        )}
        
        {frequency === 'weekly' && (
          <div className='form-group'>
            <label htmlFor='dayOfWeek'>Day of Week</label>
            <select
              name='dayOfWeek'
              value={dayOfWeek}
              onChange={onChange}
            >
              <option value='0'>Sunday</option>
              <option value='1'>Monday</option>
              <option value='2'>Tuesday</option>
              <option value='3'>Wednesday</option>
              <option value='4'>Thursday</option>
              <option value='5'>Friday</option>
              <option value='6'>Saturday</option>
            </select>
          </div>
        )}
        
        <div className='form-group'>
          <label>
            <input
              type='checkbox'
              name='isActive'
              checked={recurring.isActive}
              onChange={e => setRecurring({ ...recurring, isActive: e.target.checked })}
            />
            {' '}Active
          </label>
          <small className='form-text'>
            Inactive recurring expenses won't generate new expenses
          </small>
        </div>
        
        <input
          type='submit'
          value='Create Recurring Expense'
          className='btn btn-primary btn-block'
        />
      </form>
    </div>
  );
};

export default RecurringCreate;
