import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import FamilyContext from '../../context/family/familyContext';
import ExpenseContext from '../../context/expense/expenseContext';
import { toast } from 'react-toastify';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const ExportData = () => {
  const familyContext = useContext(FamilyContext);
  const expenseContext = useContext(ExpenseContext);
  const { familyId } = useParams();

  const { getFamily, currentFamily } = familyContext;
  const { loading } = expenseContext;

  const [filters, setFilters] = useState({
    startDate: new Date(new Date().setDate(1)), // First day of current month
    endDate: new Date(),
    category: '',
    exportType: 'expenses'
  });

  const [exportUrl, setExportUrl] = useState(null);

  useEffect(() => {
    getFamily(familyId);
    // eslint-disable-next-line
  }, [familyId]);

  const onDateChange = (date, field) => {
    setFilters({ ...filters, [field]: date });
  };

  const onChange = e => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  const generateExportUrl = () => {
    let url = '';
    
    if (filters.exportType === 'expenses') {
      url = `/api/exports/expenses/${familyId}?`;
      
      if (filters.startDate) {
        url += `startDate=${filters.startDate.toISOString()}&`;
      }
      
      if (filters.endDate) {
        url += `endDate=${filters.endDate.toISOString()}&`;
      }
      
      if (filters.category) {
        url += `category=${filters.category}`;
      }
    } else if (filters.exportType === 'monthly-report') {
      const month = filters.startDate.toLocaleString('default', { month: 'long' }).toLowerCase();
      const year = filters.startDate.getFullYear();
      url = `/api/exports/monthly-report/${familyId}/${month}/${year}`;
    }
    
    setExportUrl(url);
    return url;
  };

  const handleExport = () => {
    const url = generateExportUrl();
    
    // Create a temporary link and trigger download
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', '');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    toast.success('Export started. Your file will download shortly.');
  };

  if (loading || !currentFamily) {
    return <div className="container">Loading...</div>;
  }

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Export Data</h1>
      
      <div className="card">
        <h3>Export Options</h3>
        
        <div className="form-group">
          <label htmlFor="exportType">Export Type</label>
          <select
            name="exportType"
            value={filters.exportType}
            onChange={onChange}
          >
            <option value="expenses">Expenses CSV</option>
            <option value="monthly-report">Monthly Report</option>
          </select>
        </div>
        
        {filters.exportType === 'expenses' ? (
          <div>
            <div className="grid-2">
              <div className="form-group">
                <label>Start Date</label>
                <DatePicker
                  selected={filters.startDate}
                  onChange={date => onDateChange(date, 'startDate')}
                  className="form-control"
                  dateFormat="yyyy-MM-dd"
                />
              </div>
              <div className="form-group">
                <label>End Date</label>
                <DatePicker
                  selected={filters.endDate}
                  onChange={date => onDateChange(date, 'endDate')}
                  className="form-control"
                  dateFormat="yyyy-MM-dd"
                />
              </div>
            </div>
            
            <div className="form-group">
              <label>Category (Optional)</label>
              <select
                name="category"
                value={filters.category}
                onChange={onChange}
              >
                <option value="">All Categories</option>
                <option value="Food">Food</option>
                <option value="Transportation">Transportation</option>
                <option value="Housing">Housing</option>
                <option value="Utilities">Utilities</option>
                <option value="Entertainment">Entertainment</option>
                <option value="Healthcare">Healthcare</option>
                <option value="Education">Education</option>
                <option value="Shopping">Shopping</option>
                <option value="Other">Other</option>
              </select>
            </div>
          </div>
        ) : (
          <div className="form-group">
            <label>Month and Year</label>
            <DatePicker
              selected={filters.startDate}
              onChange={date => onDateChange(date, 'startDate')}
              className="form-control"
              dateFormat="MMMM yyyy"
              showMonthYearPicker
            />
          </div>
        )}
        
        <button onClick={handleExport} className="btn btn-primary">
          Export Data
        </button>
      </div>
      
      <div className="card bg-light my-2">
        <h3>About Data Exports</h3>
        <p>
          You can export your expense data in CSV format for use in spreadsheet applications
          or generate monthly reports summarizing your family's spending.
        </p>
        <p>
          <strong>Expenses CSV</strong> - Contains detailed information about each expense including
          date, amount, category, description, and sharing details.
        </p>
        <p>
          <strong>Monthly Report</strong> - Provides a summary of spending by category for the selected
          month, including budget information if available.
        </p>
      </div>
    </div>
  );
};

export default ExportData;
