import {
  GET_EXPENSES,
  GET_EXPENSE,
  ADD_EXPENSE,
  UPDATE_EXPENSE,
  DELETE_EXPENSE,
  EXPENSE_ERROR,
  CLEAR_EXPENSES,
  GET_EXPENSE_SUMMARY
} from '../types';

const expenseReducer = (state, action) => {
  switch (action.type) {
    case GET_EXPENSES:
      return {
        ...state,
        expenses: action.payload,
        loading: false
      };
    case GET_EXPENSE:
      return {
        ...state,
        currentExpense: action.payload,
        loading: false
      };
    case ADD_EXPENSE:
      return {
        ...state,
        expenses: [action.payload, ...state.expenses],
        loading: false
      };
    case UPDATE_EXPENSE:
      return {
        ...state,
        expenses: state.expenses.map(expense =>
          expense._id === action.payload._id ? action.payload : expense
        ),
        currentExpense: action.payload,
        loading: false
      };
    case DELETE_EXPENSE:
      return {
        ...state,
        expenses: state.expenses.filter(
          expense => expense._id !== action.payload
        ),
        loading: false
      };
    case GET_EXPENSE_SUMMARY:
      return {
        ...state,
        summary: action.payload,
        loading: false
      };
    case EXPENSE_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    case CLEAR_EXPENSES:
      return {
        ...state,
        expenses: [],
        currentExpense: null,
        summary: null,
        error: null
      };
    default:
      return state;
  }
};

export default expenseReducer;
