import React, { useContext, useEffect } from 'react';
import { Link } from 'react-router-dom';
import AuthContext from '../../context/auth/authContext';
import FamilyContext from '../../context/family/familyContext';

const Dashboard = () => {
  const authContext = useContext(AuthContext);
  const familyContext = useContext(FamilyContext);

  const { user } = authContext;
  const { families, getFamilies, loading } = familyContext;

  useEffect(() => {
    getFamilies();
    // eslint-disable-next-line
  }, []);

  return (
    <div className="container">
      <h1 className="text-primary">Dashboard</h1>
      <p className="lead">Welcome {user && user.name}</p>
      
      <div className="dashboard-actions">
        <Link to="/families/create" className="btn btn-primary">
          Create Family
        </Link>
        <Link to="/families/join" className="btn btn-dark">
          Join Family
        </Link>
      </div>

      <div className="dashboard-family-list">
        <h2>Your Families</h2>
        {families.length === 0 && !loading ? (
          <p>You don't have any families yet. Create or join one to get started!</p>
        ) : (
          <div className="family-grid">
            {families.map(family => (
              <div key={family._id} className="card">
                <h3>{family.name}</h3>
                <p>
                  Role: {family.headOfFamily === user._id ? 'Head of Family' : 'Member'}
                </p>
                <p>Members: {family.members.length}</p>
                <div>
                  <Link to={`/families/${family._id}`} className="btn btn-primary">
                    View Details
                  </Link>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Dashboard;
