import React, { useContext, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';

const FamilyMembers = () => {
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const { id } = useParams();

  const { getFamily, currentFamily, getMembers, members, transferOwnership, loading } = familyContext;
  const { user } = authContext;

  useEffect(() => {
    getFamily(id);
    getMembers(id);
    // eslint-disable-next-line
  }, [id]);

  const onTransferOwnership = async (newHeadId) => {
    if (window.confirm('Are you sure you want to transfer ownership to this member?')) {
      try {
        await transferOwnership(id, newHeadId);
      } catch (err) {
        console.error(err);
      }
    }
  };

  if (loading || !currentFamily || !members) {
    return <div className="container">Loading...</div>;
  }

  const isHead = currentFamily.headOfFamily === user._id;

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Members</h1>
      <div className="card">
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Role</th>
              {isHead && <th>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {members.map(member => (
              <tr key={member._id}>
                <td>{member.name}</td>
                <td>{member.email}</td>
                <td>
                  {member._id === currentFamily.headOfFamily
                    ? 'Head of Family'
                    : 'Member'}
                </td>
                {isHead && member._id !== user._id && (
                  <td>
                    <button
                      onClick={() => onTransferOwnership(member._id)}
                      className="btn btn-dark btn-sm"
                    >
                      Make Head
                    </button>
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default FamilyMembers;
