import React, { useContext, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import BudgetContext from '../../context/budget/budgetContext';
import FamilyContext from '../../context/family/familyContext';
import AuthContext from '../../context/auth/authContext';
import { toast } from 'react-toastify';

const BudgetList = () => {
  const budgetContext = useContext(BudgetContext);
  const familyContext = useContext(FamilyContext);
  const authContext = useContext(AuthContext);
  const { familyId } = useParams();

  const { getBudgets, budgets, deleteBudget, loading } = budgetContext;
  const { getFamily, currentFamily } = familyContext;
  const { user } = authContext;

  useEffect(() => {
    getFamily(familyId);
    getBudgets(familyId);
    // eslint-disable-next-line
  }, [familyId]);

  const onDeleteBudget = async (id) => {
    if (window.confirm('Are you sure you want to delete this budget?')) {
      try {
        await deleteBudget(id);
        toast.success('Budget deleted');
      } catch (err) {
        toast.error('Failed to delete budget');
      }
    }
  };

  if (loading || !currentFamily) {
    return <div className="container">Loading...</div>;
  }

  // Check if user is head of family
  const isHead = currentFamily.headOfFamily === user._id;

  // Group budgets by year
  const budgetsByYear = budgets.reduce((acc, budget) => {
    if (!acc[budget.year]) {
      acc[budget.year] = [];
    }
    acc[budget.year].push(budget);
    return acc;
  }, {});

  // Sort years in descending order
  const sortedYears = Object.keys(budgetsByYear).sort((a, b) => b - a);

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Budgets</h1>
      
      <div className="my-2">
        <Link to={`/budgets/create/${familyId}`} className="btn btn-primary">
          Create New Budget
        </Link>
      </div>
      
      {sortedYears.length === 0 ? (
        <div className="card">
          <p>No budgets found. Create a budget to get started!</p>
        </div>
      ) : (
        sortedYears.map(year => (
          <div key={year} className="card">
            <h3>{year}</h3>
            <table>
              <thead>
                <tr>
                  <th>Month</th>
                  <th>Total Budget</th>
                  <th>Categories</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {budgetsByYear[year]
                  .sort((a, b) => {
                    const months = ['january', 'february', 'march', 'april', 'may', 'june',
                      'july', 'august', 'september', 'october', 'november', 'december'];
                    return months.indexOf(a.month) - months.indexOf(b.month);
                  })
                  .map(budget => (
                    <tr key={budget._id}>
                      <td>{budget.month.charAt(0).toUpperCase() + budget.month.slice(1)}</td>
                      <td>${budget.totalBudget.toFixed(2)}</td>
                      <td>{budget.categories.length} categories</td>
                      <td>
                        <Link 
                          to={`/budgets/status/${familyId}/${budget.month}/${budget.year}`} 
                          className="btn btn-dark btn-sm"
                        >
                          View Status
                        </Link>
                        {isHead && (
                          <button
                            onClick={() => onDeleteBudget(budget._id)}
                            className="btn btn-danger btn-sm"
                          >
                            Delete
                          </button>
                        )}
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
        ))
      )}
    </div>
  );
};

export default BudgetList;
