import React, { useState, useContext, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import BudgetContext from '../../context/budget/budgetContext';
import FamilyContext from '../../context/family/familyContext';
import { toast } from 'react-toastify';

const BudgetCreate = () => {
  const budgetContext = useContext(BudgetContext);
  const familyContext = useContext(FamilyContext);
  const navigate = useNavigate();
  const { familyId } = useParams();

  const { createBudget, error, clearErrors } = budgetContext;
  const { getFamily, currentFamily } = familyContext;

  const [budget, setBudget] = useState({
    familyId,
    month: new Date().toLocaleString('default', { month: 'long' }).toLowerCase(),
    year: new Date().getFullYear(),
    totalBudget: '',
    categories: [
      { name: 'Food', limit: '' },
      { name: 'Transportation', limit: '' },
      { name: 'Housing', limit: '' },
      { name: 'Utilities', limit: '' },
      { name: 'Entertainment', limit: '' }
    ]
  });

  const { month, year, totalBudget, categories } = budget;

  useEffect(() => {
    getFamily(familyId);

    if (error) {
      toast.error(error);
      clearErrors();
    }
    // eslint-disable-next-line
  }, [familyId, error]);

  const onChange = e => {
    setBudget({ ...budget, [e.target.name]: e.target.value });
  };

  const onCategoryChange = (index, e) => {
    const updatedCategories = [...categories];
    updatedCategories[index] = {
      ...updatedCategories[index],
      [e.target.name]: e.target.value
    };
    
    setBudget({ ...budget, categories: updatedCategories });
  };

  const addCategory = () => {
    setBudget({
      ...budget,
      categories: [...categories, { name: '', limit: '' }]
    });
  };

  const removeCategory = index => {
    const updatedCategories = categories.filter((_, i) => i !== index);
    setBudget({ ...budget, categories: updatedCategories });
  };

  const onSubmit = async e => {
    e.preventDefault();
    
    if (totalBudget === '') {
      toast.error('Please enter a total budget');
      return;
    }
    
    // Validate categories
    const validCategories = categories.filter(cat => cat.name && cat.limit);
    if (validCategories.length === 0) {
      toast.error('Please add at least one category with a limit');
      return;
    }
    
    try {
      await createBudget({
        ...budget,
        totalBudget: parseFloat(totalBudget),
        categories: validCategories.map(cat => ({
          ...cat,
          limit: parseFloat(cat.limit)
        }))
      });
      
      toast.success('Budget created successfully!');
      navigate(`/budgets/family/${familyId}`);
    } catch (err) {
      // Error is handled by context
    }
  };

  if (!currentFamily) {
    return <div className="container">Loading...</div>;
  }

  const months = [
    'january', 'february', 'march', 'april', 'may', 'june',
    'july', 'august', 'september', 'october', 'november', 'december'
  ];

  return (
    <div className='container'>
      <h1 className='text-primary'>Create Budget</h1>
      <p className='lead'>Create a new budget for {currentFamily.name}</p>
      
      <form onSubmit={onSubmit}>
        <div className='grid-2'>
          <div className='form-group'>
            <label htmlFor='month'>Month</label>
            <select
              name='month'
              value={month}
              onChange={onChange}
              required
            >
              {months.map(m => (
                <option key={m} value={m}>
                  {m.charAt(0).toUpperCase() + m.slice(1)}
                </option>
              ))}
            </select>
          </div>
          <div className='form-group'>
            <label htmlFor='year'>Year</label>
            <input
              type='number'
              name='year'
              value={year}
              onChange={onChange}
              required
              min='2020'
              max='2030'
            />
          </div>
        </div>
        
        <div className='form-group'>
          <label htmlFor='totalBudget'>Total Budget</label>
          <input
            type='number'
            step='0.01'
            name='totalBudget'
            value={totalBudget}
            onChange={onChange}
            required
          />
        </div>
        
        <h3>Budget Categories</h3>
        {categories.map((category, index) => (
          <div key={index} className='card bg-light'>
            <div className='grid-2'>
              <div className='form-group'>
                <label htmlFor={`categoryName-${index}`}>Category Name</label>
                <input
                  type='text'
                  name='name'
                  value={category.name}
                  onChange={e => onCategoryChange(index, e)}
                  required
                />
              </div>
              <div className='form-group'>
                <label htmlFor={`categoryLimit-${index}`}>Limit</label>
                <input
                  type='number'
                  step='0.01'
                  name='limit'
                  value={category.limit}
                  onChange={e => onCategoryChange(index, e)}
                  required
                />
              </div>
            </div>
            <button
              type='button'
              className='btn btn-danger btn-sm'
              onClick={() => removeCategory(index)}
            >
              Remove Category
            </button>
          </div>
        ))}
        
        <button
          type='button'
          className='btn btn-dark my-1'
          onClick={addCategory}
        >
          Add Category
        </button>
        
        <input
          type='submit'
          value='Create Budget'
          className='btn btn-primary btn-block'
        />
      </form>
    </div>
  );
};

export default BudgetCreate;
