import React, { useContext, useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import DebtContext from '../../context/debt/debtContext';
import FamilyContext from '../../context/family/familyContext';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

const DebtHistory = () => {
  const debtContext = useContext(DebtContext);
  const familyContext = useContext(FamilyContext);
  const { familyId } = useParams();

  const { getDebtHistory, debtHistory, loading } = debtContext;
  const { getFamily, currentFamily, getMembers, members } = familyContext;

  const [filters, setFilters] = useState({
    startDate: null,
    endDate: null,
    finalized: true
  });

  useEffect(() => {
    getFamily(familyId);
    getMembers(familyId);
    getDebtHistory(familyId, { finalized: true });
    // eslint-disable-next-line
  }, [familyId]);

  const onDateChange = (date, field) => {
    setFilters({ ...filters, [field]: date });
  };

  const onFilterChange = e => {
    const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
    setFilters({ ...filters, [e.target.name]: value });
  };

  const applyFilters = () => {
    getDebtHistory(familyId, filters);
  };

  const resetFilters = () => {
    setFilters({
      startDate: null,
      endDate: null,
      finalized: true
    });
    getDebtHistory(familyId, { finalized: true });
  };

  if (loading || !currentFamily || !members) {
    return <div className="container">Loading...</div>;
  }

  // Find member names for debt display
  const getMemberName = (userId) => {
    const member = members.find(m => m._id === userId);
    return member ? member.name : 'Unknown';
  };

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Debt History</h1>
      
      <div className="card">
        <h3>Filter History</h3>
        <div className="grid-2">
          <div className="form-group">
            <label>Start Date</label>
            <DatePicker
              selected={filters.startDate}
              onChange={date => onDateChange(date, 'startDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
              isClearable
            />
          </div>
          <div className="form-group">
            <label>End Date</label>
            <DatePicker
              selected={filters.endDate}
              onChange={date => onDateChange(date, 'endDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
              isClearable
            />
          </div>
        </div>
        <div className="form-group">
          <label>
            <input
              type="checkbox"
              name="finalized"
              checked={filters.finalized}
              onChange={onFilterChange}
            />
            {' '}Show only finalized debts
          </label>
        </div>
        <div>
          <button onClick={applyFilters} className="btn btn-primary">
            Apply Filters
          </button>
          <button onClick={resetFilters} className="btn btn-light">
            Reset Filters
          </button>
        </div>
      </div>
      
      <div className="card">
        <h3>Debt History</h3>
        {debtHistory.length === 0 ? (
          <p>No debt history found for the selected filters.</p>
        ) : (
          <div>
            {debtHistory.map(debt => (
              <div key={debt._id} className="card bg-light">
                <h4>Period: {new Date(debt.startDate).toLocaleDateString()} to {new Date(debt.endDate).toLocaleDateString()}</h4>
                <p><strong>Status:</strong> {debt.finalized ? 'Finalized' : 'Not Finalized'}</p>
                <p><strong>Finalized Date:</strong> {debt.finalizedDate ? new Date(debt.finalizedDate).toLocaleDateString() : 'N/A'}</p>
                
                <h5>Debts</h5>
                <table>
                  <thead>
                    <tr>
                      <th>From</th>
                      <th>To</th>
                      <th>Amount</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {debt.simplifiedDebts.map((debtItem, index) => (
                      <tr key={index}>
                        <td>{getMemberName(debtItem.from)}</td>
                        <td>{getMemberName(debtItem.to)}</td>
                        <td>${debtItem.amount.toFixed(2)}</td>
                        <td>{debtItem.settled ? 'Settled' : 'Pending'}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
                
                <div className="my-1">
                  <Link to={`/debts/${debt._id}`} className="btn btn-dark btn-sm">
                    View Details
                  </Link>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default DebtHistory;
