import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import BudgetContext from '../../context/budget/budgetContext';
import ExpenseContext from '../../context/expense/expenseContext';
import FamilyContext from '../../context/family/familyContext';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title } from 'chart.js';
import { Pie, Bar } from 'react-chartjs-2';

// Register ChartJS components
ChartJS.register(ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title);

const BudgetStatus = () => {
  const budgetContext = useContext(BudgetContext);
  const expenseContext = useContext(ExpenseContext);
  const familyContext = useContext(FamilyContext);
  const { familyId, month, year } = useParams();

  const { getBudgetStatus, budgetStatus, loading: budgetLoading } = budgetContext;
  const { getExpenses, expenses, loading: expenseLoading } = expenseContext;
  const { getFamily, currentFamily } = familyContext;

  const [monthStart, setMonthStart] = useState(null);
  const [monthEnd, setMonthEnd] = useState(null);

  useEffect(() => {
    getFamily(familyId);
    
    // Calculate month start and end dates
    const monthIndex = ['january', 'february', 'march', 'april', 'may', 'june',
      'july', 'august', 'september', 'october', 'november', 'december'].indexOf(month.toLowerCase());
    
    if (monthIndex !== -1) {
      const start = new Date(parseInt(year), monthIndex, 1);
      const end = new Date(parseInt(year), monthIndex + 1, 0);
      
      setMonthStart(start);
      setMonthEnd(end);
      
      getBudgetStatus(familyId, month.toLowerCase(), year);
      getExpenses(familyId, { startDate: start.toISOString(), endDate: end.toISOString() });
    }
    // eslint-disable-next-line
  }, [familyId, month, year]);

  if (budgetLoading || expenseLoading || !currentFamily || !budgetStatus) {
    return <div className="container">Loading...</div>;
  }

  // Prepare data for category pie chart
  const categoryData = {
    labels: budgetStatus.categories.map(cat => cat.name),
    datasets: [
      {
        data: budgetStatus.categories.map(cat => cat.spent),
        backgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56',
          '#4BC0C0',
          '#9966FF',
          '#FF9F40',
          '#8AC926',
          '#1982C4',
          '#6A4C93'
        ]
      }
    ]
  };

  // Prepare data for budget vs spent bar chart
  const barData = {
    labels: budgetStatus.categories.map(cat => cat.name),
    datasets: [
      {
        label: 'Budget',
        data: budgetStatus.categories.map(cat => cat.limit),
        backgroundColor: '#36A2EB'
      },
      {
        label: 'Spent',
        data: budgetStatus.categories.map(cat => cat.spent),
        backgroundColor: '#FF6384'
      }
    ]
  };

  const barOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Budget vs Actual Spending'
      }
    },
    scales: {
      y: {
        beginAtZero: true
      }
    }
  };

  // Calculate percentage of total budget spent
  const percentSpent = (budgetStatus.totalSpent / budgetStatus.totalBudget) * 100;
  const progressBarColor = percentSpent > 100 ? '#dc3545' : percentSpent > 80 ? '#ffc107' : '#28a745';

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Budget Status</h1>
      <h2>{month.charAt(0).toUpperCase() + month.slice(1)} {year}</h2>
      
      <div className="card">
        <h3>Budget Overview</h3>
        <div className="budget-status">
          <div>
            <p><strong>Total Budget:</strong> ${budgetStatus.totalBudget.toFixed(2)}</p>
            <p><strong>Total Spent:</strong> ${budgetStatus.totalSpent.toFixed(2)}</p>
            <p><strong>Remaining:</strong> ${(budgetStatus.totalBudget - budgetStatus.totalSpent).toFixed(2)}</p>
          </div>
          <div>
            <p><strong>Period:</strong> {monthStart && monthStart.toLocaleDateString()} to {monthEnd && monthEnd.toLocaleDateString()}</p>
            <p><strong>Number of Expenses:</strong> {expenses.length}</p>
            <p><strong>Budget Status:</strong> {percentSpent > 100 ? 'Over Budget' : percentSpent > 80 ? 'Warning' : 'On Track'}</p>
          </div>
        </div>
        
        <div>
          <p><strong>Budget Progress:</strong> {percentSpent.toFixed(1)}%</p>
          <div className="budget-progress">
            <div 
              className="budget-progress-bar" 
              style={{ 
                width: `${Math.min(percentSpent, 100)}%`,
                backgroundColor: progressBarColor
              }}
            ></div>
          </div>
        </div>
      </div>
      
      <div className="grid-2 my-2">
        <div className="card">
          <h3>Spending by Category</h3>
          <div style={{ height: '300px' }}>
            {budgetStatus.categories.length > 0 ? (
              <Pie data={categoryData} options={{ maintainAspectRatio: false }} />
            ) : (
              <p>No category data available</p>
            )}
          </div>
        </div>
        <div className="card">
          <h3>Budget vs Actual</h3>
          <div style={{ height: '300px' }}>
            {budgetStatus.categories.length > 0 ? (
              <Bar data={barData} options={{ ...barOptions, maintainAspectRatio: false }} />
            ) : (
              <p>No category data available</p>
            )}
          </div>
        </div>
      </div>
      
      <div className="card">
        <h3>Category Details</h3>
        <table>
          <thead>
            <tr>
              <th>Category</th>
              <th>Budget</th>
              <th>Spent</th>
              <th>Remaining</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {budgetStatus.categories.map((category, index) => (
              <tr key={index}>
                <td>{category.name}</td>
                <td>${category.limit.toFixed(2)}</td>
                <td>${category.spent.toFixed(2)}</td>
                <td>${(category.limit - category.spent).toFixed(2)}</td>
                <td>
                  {category.spent > category.limit ? (
                    <span className="text-danger">Over Budget</span>
                  ) : category.spent > category.limit * 0.8 ? (
                    <span className="text-warning">Warning</span>
                  ) : (
                    <span className="text-success">On Track</span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default BudgetStatus;
