import {
  GET_RECURRING,
  GET_RECURRING_EXPENSE,
  CREATE_RECURRING,
  UPDATE_RECURRING,
  DELETE_RECURRING,
  GENERATE_EXPENSE,
  RECURRING_ERROR,
  CLEAR_RECURRING
} from '../types';

const recurringReducer = (state, action) => {
  switch (action.type) {
    case GET_RECURRING:
      return {
        ...state,
        recurringExpenses: action.payload,
        loading: false
      };
    case GET_RECURRING_EXPENSE:
      return {
        ...state,
        currentRecurring: action.payload,
        loading: false
      };
    case CREATE_RECURRING:
      return {
        ...state,
        recurringExpenses: [action.payload, ...state.recurringExpenses],
        loading: false
      };
    case UPDATE_RECURRING:
      return {
        ...state,
        recurringExpenses: state.recurringExpenses.map(recurring =>
          recurring._id === action.payload._id ? action.payload : recurring
        ),
        currentRecurring: action.payload,
        loading: false
      };
    case DELETE_RECURRING:
      return {
        ...state,
        recurringExpenses: state.recurringExpenses.filter(
          recurring => recurring._id !== action.payload
        ),
        loading: false
      };
    case GENERATE_EXPENSE:
      return {
        ...state,
        loading: false
      };
    case RECURRING_ERROR:
      return {
        ...state,
        error: action.payload,
        loading: false
      };
    case CLEAR_RECURRING:
      return {
        ...state,
        recurringExpenses: [],
        currentRecurring: null,
        error: null
      };
    default:
      return state;
  }
};

export default recurringReducer;
