import React, { useReducer } from 'react';
import axios from 'axios';
import ExpenseContext from './expenseContext';
import expenseReducer from './expenseReducer';
import {
  GET_EXPENSES,
  GET_EXPENSE,
  ADD_EXPENSE,
  UPDATE_EXPENSE,
  DELETE_EXPENSE,
  EXPENSE_ERROR,
  CLEAR_EXPENSES,
  GET_EXPENSE_SUMMARY
} from '../types';

const ExpenseState = props => {
  const initialState = {
    expenses: [],
    currentExpense: null,
    summary: null,
    loading: true,
    error: null
  };

  const [state, dispatch] = useReducer(expenseReducer, initialState);

  // Get expenses for a family
  const getExpenses = async (familyId, filters = {}) => {
    try {
      let url = `/api/expenses/family/${familyId}`;
      
      // Add query parameters for filters
      const queryParams = [];
      if (filters.startDate) queryParams.push(`startDate=${filters.startDate}`);
      if (filters.endDate) queryParams.push(`endDate=${filters.endDate}`);
      if (filters.category) queryParams.push(`category=${filters.category}`);
      if (filters.userId) queryParams.push(`userId=${filters.userId}`);
      
      if (queryParams.length > 0) {
        url += `?${queryParams.join('&')}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_EXPENSES,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Get specific expense
  const getExpense = async id => {
    try {
      const res = await axios.get(`/api/expenses/${id}`);

      dispatch({
        type: GET_EXPENSE,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Add expense
  const addExpense = async expense => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post('/api/expenses', expense, config);

      dispatch({
        type: ADD_EXPENSE,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Update expense
  const updateExpense = async (id, expense) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.put(`/api/expenses/${id}`, expense, config);

      dispatch({
        type: UPDATE_EXPENSE,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Delete expense
  const deleteExpense = async id => {
    try {
      await axios.delete(`/api/expenses/${id}`);

      dispatch({
        type: DELETE_EXPENSE,
        payload: id
      });
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Get expense summary
  const getExpenseSummary = async (familyId, filters = {}) => {
    try {
      let url = `/api/expenses/summary/${familyId}`;
      
      // Add query parameters for filters
      const queryParams = [];
      if (filters.startDate) queryParams.push(`startDate=${filters.startDate}`);
      if (filters.endDate) queryParams.push(`endDate=${filters.endDate}`);
      
      if (queryParams.length > 0) {
        url += `?${queryParams.join('&')}`;
      }
      
      const res = await axios.get(url);

      dispatch({
        type: GET_EXPENSE_SUMMARY,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: EXPENSE_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Clear expenses
  const clearExpenses = () => {
    dispatch({ type: CLEAR_EXPENSES });
  };

  return (
    <ExpenseContext.Provider
      value={{
        expenses: state.expenses,
        currentExpense: state.currentExpense,
        summary: state.summary,
        loading: state.loading,
        error: state.error,
        getExpenses,
        getExpense,
        addExpense,
        updateExpense,
        deleteExpense,
        getExpenseSummary,
        clearExpenses
      }}
    >
      {props.children}
    </ExpenseContext.Provider>
  );
};

export default ExpenseState;
