import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ExpenseContext from '../../context/expense/expenseContext';
import FamilyContext from '../../context/family/familyContext';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title } from 'chart.js';
import { Pie, Bar } from 'react-chartjs-2';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";

// Register ChartJS components
ChartJS.register(ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title);

const ExpenseSummary = () => {
  const expenseContext = useContext(ExpenseContext);
  const familyContext = useContext(FamilyContext);
  const { familyId } = useParams();

  const { getExpenseSummary, summary, loading } = expenseContext;
  const { getFamily, currentFamily } = familyContext;

  const [filters, setFilters] = useState({
    startDate: new Date(new Date().setDate(1)), // First day of current month
    endDate: new Date()
  });

  useEffect(() => {
    getFamily(familyId);
    getExpenseSummary(familyId, filters);
    // eslint-disable-next-line
  }, [familyId]);

  const onDateChange = (date, field) => {
    setFilters({ ...filters, [field]: date });
  };

  const applyFilters = () => {
    getExpenseSummary(familyId, filters);
  };

  if (loading || !currentFamily || !summary) {
    return <div className="container">Loading...</div>;
  }

  // Prepare data for category pie chart
  const categoryData = {
    labels: summary.byCategory.map(item => item._id),
    datasets: [
      {
        data: summary.byCategory.map(item => item.total),
        backgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56',
          '#4BC0C0',
          '#9966FF',
          '#FF9F40',
          '#8AC926',
          '#1982C4',
          '#6A4C93'
        ]
      }
    ]
  };

  // Prepare data for member bar chart
  const memberData = {
    labels: summary.byMember.map(item => item.memberName || 'Unknown'),
    datasets: [
      {
        label: 'Amount Spent',
        data: summary.byMember.map(item => item.total),
        backgroundColor: '#36A2EB'
      }
    ]
  };

  const barOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Expenses by Member'
      }
    }
  };

  return (
    <div className="container">
      <h1 className="text-primary">{currentFamily.name} - Expense Summary</h1>
      
      <div className="card">
        <h3>Filter Period</h3>
        <div className="grid-2">
          <div className="form-group">
            <label>Start Date</label>
            <DatePicker
              selected={filters.startDate}
              onChange={date => onDateChange(date, 'startDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
            />
          </div>
          <div className="form-group">
            <label>End Date</label>
            <DatePicker
              selected={filters.endDate}
              onChange={date => onDateChange(date, 'endDate')}
              className="form-control"
              dateFormat="yyyy-MM-dd"
            />
          </div>
        </div>
        <button onClick={applyFilters} className="btn btn-primary">
          Apply Filters
        </button>
      </div>
      
      <div className="card">
        <h3>Summary Statistics</h3>
        <div className="grid-2">
          <div>
            <p><strong>Total Expenses:</strong> ${summary.totalAmount.toFixed(2)}</p>
            <p><strong>Number of Expenses:</strong> {summary.count}</p>
            <p><strong>Average Expense:</strong> ${(summary.totalAmount / (summary.count || 1)).toFixed(2)}</p>
          </div>
          <div>
            <p><strong>Period:</strong> {new Date(filters.startDate).toLocaleDateString()} to {new Date(filters.endDate).toLocaleDateString()}</p>
            <p><strong>Highest Expense:</strong> ${summary.highest ? summary.highest.amount.toFixed(2) : '0.00'} ({summary.highest ? summary.highest.description : 'None'})</p>
            <p><strong>Most Common Category:</strong> {summary.mostCommonCategory || 'None'}</p>
          </div>
        </div>
      </div>
      
      <div className="grid-2 my-2">
        <div className="card">
          <h3>Expenses by Category</h3>
          <div style={{ height: '300px' }}>
            {summary.byCategory.length > 0 ? (
              <Pie data={categoryData} options={{ maintainAspectRatio: false }} />
            ) : (
              <p>No category data available</p>
            )}
          </div>
        </div>
        <div className="card">
          <h3>Expenses by Member</h3>
          <div style={{ height: '300px' }}>
            {summary.byMember.length > 0 ? (
              <Bar data={memberData} options={{ ...barOptions, maintainAspectRatio: false }} />
            ) : (
              <p>No member data available</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExpenseSummary;
