import React, { useReducer } from 'react';
import axios from 'axios';
import FamilyContext from './familyContext';
import familyReducer from './familyReducer';
import {
  GET_FAMILIES,
  GET_FAMILY,
  CREATE_FAMILY,
  JOIN_FAMILY,
  FAMILY_ERROR,
  CLEAR_FAMILY,
  GET_MEMBERS,
  LEAVE_FAMILY,
  TRANSFER_OWNERSHIP
} from '../types';

const FamilyState = props => {
  const initialState = {
    families: [],
    currentFamily: null,
    members: [],
    loading: true,
    error: null
  };

  const [state, dispatch] = useReducer(familyReducer, initialState);

  // Get user's families
  const getFamilies = async () => {
    try {
      const res = await axios.get('/api/families');

      dispatch({
        type: GET_FAMILIES,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Get specific family
  const getFamily = async id => {
    try {
      const res = await axios.get(`/api/families/${id}`);

      dispatch({
        type: GET_FAMILY,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Create family
  const createFamily = async formData => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post('/api/families', formData, config);

      dispatch({
        type: CREATE_FAMILY,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Join family
  const joinFamily = async inviteCode => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.post('/api/families/join', { inviteCode }, config);

      dispatch({
        type: JOIN_FAMILY,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Get family members
  const getMembers = async familyId => {
    try {
      const res = await axios.get(`/api/families/${familyId}/members`);

      dispatch({
        type: GET_MEMBERS,
        payload: res.data
      });
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg
      });
    }
  };

  // Leave family
  const leaveFamily = async familyId => {
    try {
      await axios.delete(`/api/families/${familyId}/leave`);

      dispatch({
        type: LEAVE_FAMILY,
        payload: familyId
      });
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg
      });
      throw err;
    }
  };

  // Transfer ownership
  const transferOwnership = async (familyId, newHeadId) => {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    try {
      const res = await axios.put(
        `/api/families/${familyId}/transfer-ownership`,
        { newHeadId },
        config
      );

      dispatch({
        type: TRANSFER_OWNERSHIP,
        payload: res.data
      });

      return res.data;
    } catch (err) {
      dispatch({
        type: FAMILY_ERROR,
        payload: err.response.data.msg || err.response.data.errors[0].msg
      });
      throw err;
    }
  };

  // Clear current family
  const clearFamily = () => {
    dispatch({ type: CLEAR_FAMILY });
  };

  return (
    <FamilyContext.Provider
      value={{
        families: state.families,
        currentFamily: state.currentFamily,
        members: state.members,
        loading: state.loading,
        error: state.error,
        getFamilies,
        getFamily,
        createFamily,
        joinFamily,
        getMembers,
        leaveFamily,
        transferOwnership,
        clearFamily
      }}
    >
      {props.children}
    </FamilyContext.Provider>
  );
};

export default FamilyState;
